import shuffle from 'lodash.shuffle';

const NUMERO_DE_CARTAS = 20;

export default () =>  {

  const figuras = ["arma", "asegura", "coloca", "cuida", "establece", "identifica", "informate", "no_propagues", "no_tires", "recoge", "resguarda", "revisa"]

  const mensajes = ["<b>Prepar</b> un equipo de seguridad que puedas tener a disposicin en caso de emergencia. Los elementos bsicos que debe contener son: <div style='text-align: left;'><ul><li>Agua potable embotellada.</li> <li>Alimentos envasados o enlatados.</li><li>Linterna con pilas (repuesto de pilas).</li><li>Radio a pilas (repuesto de pilas). </li><li>Documentos y dinero (preferentemente guardarlos en bolsa impermeable). </li><li>Muda seca de ropa interior, vestimenta bsica y algn abrigo (preferentemente guardarla en bolsa impermeable). </li><li>Medicamentos. </li><li>Un botiqun de primeros auxilios. </li></div>",
                    "<b>Asegur</b> que todas/os sepan cmo <b>cortar</b> los servicios de agua, luz y gas. Ser una de las primeras cosas que debern hacer en caso de emergencia!",
                    "<b>Coloc</b> todos los productos peligrosos o txicos (productos de limpieza, combustibles, cidos, venenos, desinfectantes, etc.) bien cerrados a una altura prudente donde no los alcance el agua en una eventual inundacin, as evitamos el riesgo de contaminacin. Cuid que no se viertan combustibles, cidos y solventes, ya que pueden producir daos en la salud, reacciones peligrosas e incluso explosivas.",
                    "<b>Debemos</b> tomar el hbito de mantener limpios todos los espacios que frecuentamos, es decir: no tirar basura al piso, a las zanjas o arroyos, sino hacerlo en los lugares permitidos; evitar la acumulacin de escombros, residuos, etc. En caso de lluvias esto es fundamental para que el agua pueda drenar y tomar su curso natural.",
                    "<b>Establec</b> un lugar seguro en la casa o acord con una vecina/o cercano para acudir en caso de que sea necesario. <b>Importante!</b> Si bien debe ser evaluado como la ltima opcin, es decir en caso de no tener la posibilidad de acudir a otro lugar seguro, es bueno saber dnde se encuentra el <b>Punto de Encuentro</b> ms prximo, estos estn disponibles para recibir y dar seguridad a las personas damnificadas.",
                    "<b>Identific</b> qu muebles o ropa pueden verse afectados por el agua. Planific cmo podras elevarlos en caso de precisarlo y de ser posible, adelantate y colocalos en un lugar alto, te ahorrar mucha energa si hay una emergencia!",
                    "<b>Mantenerse actualizado</b> en cuanto al <b>Plan General de Gestin de Riesgo Municipal</b> es muy importante, identificando dnde se encuentran los Centros de Salud, Puntos de Encuentro, Delegaciones Municipales, Destacamentos de Bomberos, Comisaras y dems informacin necesaria para saber cmo se gestiona la emergencia en tu barrio y los centros de evacuacin. Si no los conocs, pods consultar en el anexo de mapas de este manual o ingresar al portal web del Plan RRI en https://quehacerlaplata.org/",
                    "<b>No propagues rumores</b> o informaciones exageradas sobre la situacin. Si te llega algn mensaje escrito o en audio por cualquier red social o servicio de mensajera, revis las fuentes oficiales para corroborar la informacin. Debemos evitar el miedo y el caos que podran provocar los falsos rumores.",
                    "<b>No saques</b> los residuos durante las tormentas, ya que estos podran obstruir el paso del agua. Es importante saber cundo sacarlos <b>este pequeo hbito repercute en la salud de la ciudad!</b>",
                    "<b>Recog</b> la basura, escombros, ramas y hojas de tu casa para evitar que obstaculicen el paso del agua durante lluvias intensas y desborde de arroyos.",
                    "<b>Coloc</b> en una bolsa hermtica y en un lugar seguro los documentos, partidas de nacimiento, libretas sanitarias, pasaportes, plizas de seguros, escrituras y cualquier otro documento de valor. Todas las personas que viven en la casa deben conocer la ubicacin de estos documentos para ponerlos a salvo.",
                    "<b>Revis</b> peridicamente el estado de techos, canaletas, rejillas y desages pluviales de tu casa. Sell posibles filtraciones con algn material impermeable. <b>Importante! Ten especial cuidado al eliminar cualquier tipo de elemento cortante que se acumule en techos, canaletas y desages ya que puede ser arrastrado con el agua y lastimar a alguien.</b>"
                    ];

                    
  const etiquetas = ["Mochila y equipo de seguridad.",
                     "Cerrar perillas.",
                     "Material toxico a resguardar en zonas altas.",
                     "Bolsa de basura. Smbolo reciclaje.",
                     "Punto de encuentro.",
                     "Muebles y ropa a resguardar en zonas altas.",
                     "Smartphones. Google play.",
                     "Televisor, megfono y redes sociales.",
                     "Basura, restos de manzana, hueso, cscara de huevo.",
                     "Bolsa de basura, ramas, escombros, manos.",
                     "Documentos importantes dentro de Ziplok.",
                     "Botella, recipiente, vaso roto, smbolo peligro."
                    ];

  let cartas = [];

  while (cartas.length < NUMERO_DE_CARTAS) {
    const index = Math.floor(Math.random() * figuras.length);
    const carta = {
      fila: 0,
      columna: 0,
      icono: figuras.splice(index, 1)[0],
      mensaje: mensajes.splice(index, 1)[0],
      etiqueta: etiquetas.splice(index, 1)[0],
      fueAdivinada: false
    };

    cartas.push(carta);
    cartas.push({...carta});
  }

  cartas = shuffle(cartas);


  for (var fila = 1; fila <= 4; fila++) {
    for (var columna = 1; columna <= 5; columna++) {
      var index = (fila - 1) * 5 + (columna - 1)
      cartas[index].fila = fila
      cartas[index].columna = columna
    }
 }

  return cartas;

  
};



